h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.swiper.pageSwiper {
  max-width: 50%;
  height: 100%;
}
@media screen and (max-width: 1023px) {
  .swiper.pageSwiper {
    width: auto;
  }
}

.swiper-slide.only-slide {
  text-align: center;
  font-size: 18px;
  background: none;
  min-width: 194px;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide.only-slide img {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.swiper-wrapper {
  list-style: none;
}

