.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 414px) {
  .container {
    width: 414px;
  }
}
@media (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

.hero-button {
  padding: 14px 35px;
  border: 2px solid #ffffff;
  border-radius: 25px;
  background-color: rgba(0, 0, 0, 0.4);
  color: #f3c653;
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.02em;
}

.media-list {
  display: flex;
  flex-direction: row;
  gap: 9px;
  padding-bottom: 20px;
}
.media-list__icon {
  fill: rgba(243, 198, 83, 0.7);
}

.address__control {
  display: flex;
  flex-direction: row-reverse;
  padding-right: 15px;
}
.address__element {
  display: inline-flex;
  gap: 8px;
  justify-content: center;
  align-items: center;
  width: 210px;
  padding: 8px 0 8px 6px;
  border: 1px solid rgba(243, 198, 83, 0.7);
  border-radius: 2px;
}
.address__link {
  font-family: "Montserrat";
  font-style: normal;
  font-size: 12px;
  line-height: 1.25;
  letter-spacing: 0.01em;
  color: #E5E5E5;
}
.address__icon {
  fill: #F3C653;
}

.hero {
  max-width: 1280px;
  margin: auto;
  padding-top: 64px;
  background-position: center;
  background-size: cover;
  background-image: url(../images/hero/hero-home-mobile.jpg);
}
@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .hero {
    background-image: url(../images/hero/hero-home-mobile@2x.jpg);
  }
}
.hero__title {
  margin-top: 70px;
  font-family: "Film P3";
  font-size: 96px;
  line-height: 1.07;
  letter-spacing: 0.3em;
  color: #FFFFFF;
  text-align: center;
}
.hero__subtitle {
  margin-top: 32px;
  font-family: "Montserrat";
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  text-align: center;
}
.hero__motto {
  margin-top: 44px;
  font-family: "Times New Roman";
  font-weight: 700;
  font-size: 32px;
  line-height: 1.15;
  letter-spacing: 0.05em;
  color: #FDFBEC;
  text-align: center;
}
.hero__control-buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-top: 113px;
}
.hero__media {
  margin-top: 58px;
  padding-left: 15px;
}

.restaurant {
  position: relative;
  padding-top: 16px;
  padding-bottom: 20px;
  background-color: #fffdee;
  background-image: linear-gradient(to right, transparent 33.8%, rgba(186, 186, 186, 0.4) 33.8% 33.9%, transparent 33.9% 66.1%, rgba(186, 186, 186, 0.4) 66.1% 66.2%, transparent 66.2%);
}
@media screen and (min-width: 1024px) {
  .restaurant {
    padding-top: 120px;
    padding-bottom: 32px;
    background-image: linear-gradient(to right, transparent 27.1%, rgba(186, 186, 186, 0.4) 27.1% 27.2%, transparent 27.2% 50%, rgba(186, 186, 186, 0.4) 50% 50.1%, transparent 50.1% 72.4%, rgba(186, 186, 186, 0.4) 72.4% 72.5%, transparent 72.5%);
  }
}
@media screen and (min-width: 1280px) {
  .restaurant {
    padding-bottom: 50px;
    background-image: linear-gradient(to right, transparent 26.4%, rgba(186, 186, 186, 0.4) 26.4% 26.5%, transparent 26.5% 50%, rgba(186, 186, 186, 0.4) 50% 50.1%, transparent 50.1% 73.6%, rgba(186, 186, 186, 0.4) 73.6% 73.7%, transparent 73.7%);
  }
