h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

*, :before, :after {
  box-sizing: border-box;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.swiper.pageSwiper {
  max-width: 50%;
  height: 100%;
}

@media screen and (max-width: 1023px) {
  .swiper.pageSwiper {
    width: auto;
  }
}

.swiper-slide.only-slide {
  text-align: center;
  min-width: 194px;
  background: none;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  display: flex;
}

.swiper-slide.only-slide img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  display: block;
}

.swiper-wrapper {
  list-style: none;
}

