.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (min-width: 414px) {
  .container {
    width: 414px;
  }
}

@media (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

.hero-button {
  color: #f3c653;
  letter-spacing: .02em;
  background-color: rgba(0, 0, 0, .4);
  border: 2px solid #fff;
  border-radius: 25px;
  padding: 14px 35px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
}

.media-list {
  flex-direction: row;
  gap: 9px;
  padding-bottom: 20px;
  display: flex;
}

.media-list__icon {
  fill: rgba(243, 198, 83, .7);
}

.address__control {
  flex-direction: row-reverse;
  padding-right: 15px;
  display: flex;
}

.address__element {
  width: 210px;
  border: 1px solid rgba(243, 198, 83, .7);
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 8px 0 8px 6px;
  display: inline-flex;
}

.address__link {
  letter-spacing: .01em;
  color: #e5e5e5;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  line-height: 1.25;
}

.address__icon {
  fill: #f3c653;
}

.hero {
  max-width: 1280px;
  background-image: url("hero-home-mobile.9649fcc3.jpg");
  background-position: center;
  background-size: cover;
  margin: auto;
  padding-top: 64px;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .hero {
    background-image: url("hero-home-mobile@2x.7687ecfb.jpg");
  }
}

.hero__title {
  letter-spacing: .3em;
  color: #fff;
  text-align: center;
  margin-top: 70px;
  font-family: Film P3;
  font-size: 96px;
  line-height: 1.07;
}

.hero__subtitle {
  letter-spacing: .02em;
  color: #fff;
  text-align: center;
  margin-top: 32px;
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
}

.hero__motto {
  letter-spacing: .05em;
  color: #fdfbec;
  text-align: center;
  margin-top: 44px;
  font-family: Times New Roman;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.15;
}

.hero__control-buttons {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-top: 113px;
  display: flex;
}

.hero__media {
  margin-top: 58px;
  padding-left: 15px;
}

.restaurant {
  background-color: #fffdee;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0) 33.8%, rgba(186, 186, 186, .4) 33.8%, rgba(186, 186, 186, .4) 33.9%, rgba(0, 0, 0, 0) 33.9%, rgba(0, 0, 0, 0) 66.1%, rgba(186, 186, 186, .4) 66.1%, rgba(186, 186, 186, .4) 66.2%, rgba(0, 0, 0, 0) 66.2%);
  padding-top: 16px;
  padding-bottom: 20px;
  position: relative;
}

@media screen and (min-width: 1024px) {
  .restaurant {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0) 27.1%, rgba(186, 186, 186, .4) 27.1%, rgba(186, 186, 186, .4) 27.2%, rgba(0, 0, 0, 0) 27.2%, rgba(0, 0, 0, 0) 50%, rgba(186, 186, 186, .4) 50%, rgba(186, 186, 186, .4) 50.1%, rgba(0, 0, 0, 0) 50.1%, rgba(0, 0, 0, 0) 72.4%, rgba(186, 186, 186, .4) 72.4%, rgba(186, 186, 186, .4) 72.5%, rgba(0, 0, 0, 0) 72.5%);
    padding-top: 120px;
    padding-bottom: 32px;
  }
}

